import React, { useEffect, useRef, useState } from "react";
import Isotope from "isotope-layout";
import ProjectDetailsModal from "./ProjectDetailsModal";
const Portfolio = ({ classicHeader, darkTheme }) => {
  // init one ref to store the future isotope object
  const isotope = useRef();
  // store the filter keyword in a state
  const [filterKey, setFilterKey] = useState("*");
  const [imagesLoaded, setimagesLoaded] = useState(0);
  const [selectedProjectDetails, setSelectedProjectDetails] = useState();

  const filters = {
    RENDERING: "Rendering",
    GAME: "Game",
    OTHERS: "Others",
  };

  // 2d games - circle world, alc, wiz, tic
  // 3d game
  // club game
  // caustic
  // rendering - ray tracing, path tracing
  // Others - Geometry processing, FEM, ARAP
  const projectsData = [
    {
      title: "Stylized Caustics: Progressive Rendering of Animated Caustics",
      projectInfo:
          "Implemented the techniques introduced in the paper Stylized Caustics: Progressive Rendering of Animated Caustics and reproduced its results. " +
          "By assigning photons to different pixels and moving photons as time goes by, we can produce caustics animation.",
      technologies: "C++ / sampling / projection / greedy algorithm / ...",
      date: "2023/4 - 2023/5",
      url: {
        name: ["Github repo", "Google slides", "Original paper"],
        link: ["https://github.com/JARVISHHH/Y_Caustics", "https://docs.google.com/presentation/d/1m1_nMif3nax9rD_H_DbY-5OQGuu9LBRSLKtyXCdANTA/edit#slide=id.g9ab8b2cfe6_1_0", "https://vc.cs.ovgu.de/media/publications/2016/Guenther_2016_EGb.pdf"],
        exist: true,
        size: 3,
      },
      thumbImage: "images/projects/Caustics/deer.gif",
      sliderImages: [
        "images/projects/Caustics/multiple.gif",
        "images/projects/Caustics/animation.gif",
        "images/projects/Caustics/cool.gif",
      ],
      categories: [filters.RENDERING],
    },
    {
      title: "Renderers for fun - Ray Tracing and Path Tracing",
      projectInfo:
          "An application that integrated different renderers and provided GUI. Users can upload different scene files, choose different renderers and features. The project is still in progress.",
      technologies: "C++ / phong illumination model / soft shadow / texture mapping / importance sampling / ...",
      date: "2023/3 - present",
      url: {
        name: ["Github repo (to be done)"],
        link: ["https://github.com/JARVISHHH"],
        exist: true,
        size: 1,
      },
      thumbImage: "images/projects/Renderers/refraction.png",
      sliderImages: [
        "images/projects/Renderers/sphere.png",
        "images/projects/Renderers/chainsawman.png",
      ],
      categories: [filters.RENDERING],
    },
    {
      title: "Realtime rendering based on OpenGL",
      projectInfo:
          "A realtime rendering application based on OpenGL. " +
          "Users can control shape tessellation, move camera, choose different filters and etc.",
      technologies: "C++ / OpenGL / tessellation / camera movement / ...",
      date: "2022/11 - 2022/12",
      url: {
        exist: false,
      },
      thumbImage: "images/projects/Renderers/realtime.gif",
      sliderImages: [
        "images/projects/Renderers/realtime_mario.png",
        "images/projects/Renderers/realtime_chess.png",
        "images/projects/Renderers/realtime.png",
      ],
      categories: [filters.RENDERING],
    },
    {
      title: "Escape - a game based on self-made 3D game engine",
      projectInfo:
          "A dungeon escape rogue-like game produced on a self-made 3D game engine. " +
          "Players need to explore different rooms in a dungeon, kill different enemies, get pick-ups and escape the dungeon.",
      technologies: "C++ / collision / AI / rendering / space partition / UI / ...",
      date: "2023/2 - 2023/5",
      url: {
        name: ["Github repo (demo video included)", "Google slides"],
        link: ["https://github.com/JARVISHHH/Escape", "https://docs.google.com/presentation/d/1uaBW2uZxgA3INfNzQe-RBvGKkVebQzpuPwg3CGzBgKc/edit#slide=id.p"],
        exist: true,
        size: 2,
      },
      thumbImage: "images/projects/Escape/title.jpg",
      sliderImages: [
        "images/projects/Escape/instruction.jpg",
        "images/projects/Escape/ingame1.jpg",
        "images/projects/Escape/ingame2.jpg",
        "images/projects/Escape/ingame3.jpg",
        "images/projects/Escape/win.jpg",
      ],
      categories: [filters.GAME],
    },
    {
      title: "Circle World - a game based on self-made 2D game engine",
      projectInfo:
        "A 2D platformer game implemented produced on a self-made 2D game engine. " +
          "Players will control a circle to get over all the obstacles to get to the goal. " +
          "Watch out those spikes, they may attack you before you even notice.",
      technologies: "Java / collision / sound / video / UI / ...",
      date: "2022/9 - 2022/12",
      url: {
        name: ["Github repo (demo video included)"],
        link: ["https://github.com/JARVISHHH/CircleWorld"],
        exist: true,
        size: 1,
      },
      thumbImage: "images/projects/CircleWorld/title.jpg",
      sliderImages: [
        "images/projects/CircleWorld/ingame1.jpg",
        "images/projects/CircleWorld/ingame2.jpg",
        "images/projects/CircleWorld/gameover.jpg",
        "images/projects/CircleWorld/clear.jpg",
      ],
      categories: [filters.GAME],
    },
    {
      title: "Demo games based on self-made 2D game engine",
      projectInfo:
        "Demo games produced on a self-made 2D game engine, " +
          "including wizardry (kill enemies, collect pick-ups and find your way out), alchemy (combine different characters to get different new characters) and tic tac toe (tic tac toe).",
      technologies: "Java / space partition / collision / animation / AI / ...",
      date: "2022/9 - 2022/11",
      url: {
        exist: false,
      },
      thumbImage: "images/projects/DemoGames/wiz_demo.gif",
      sliderImages: [
        "images/projects/DemoGames/alc_demo.gif",
        "images/projects/DemoGames/tic_demo.gif",
      ],
      categories: [filters.GAME],
    },
    {
      title: "Recurring nightmare",
      projectInfo:
        "A 2D platformer survival game produced by Unity.",
      technologies: "C# / Unity / pathfinding / animation / sound / ...",
      date: "2023/2 - 2023/5",
      url: {
        exist: false,
      },
      thumbImage: "images/projects/RecurringNightmare/title.jpg",
      sliderImages: [
        "images/projects/RecurringNightmare/ingame1.jpg",
        "images/projects/RecurringNightmare/ingame2.jpg",
        "images/projects/RecurringNightmare/ingame3.jpg",
        "images/projects/RecurringNightmare/ingame4.jpg",
      ],
      categories: [filters.GAME],
    },
    {
      title: "As-Rigid-As-Possible Surface Modeling (ARAP)",
      projectInfo:
        "It's a system for user-interactive deformation of 3D meshes. The system implemented core techniques introduced by the paper As-Rigid-As-Possible Surface Modeling (ARAP). " +
          "In the system, mesh vertices can be re-positioned by clicking and dragging. The system will update the rest of the mesh in response to these user interactions such that it moves as-rigidly-as-possible",
      technologies: "C++",
      date: "2023/3 - 2023/4",
      url: {
        name:["Original paper"],
        link:["https://igl.ethz.ch/projects/ARAP/arap_web.pdf"],
        exist: true,
        size: 1,
      },
      thumbImage: "images/projects/Others/wave_armadillo.gif",
      sliderImages: [
        "images/projects/Others/twopoint_teapot.gif",
        "images/projects/Others/deformation_bean.gif",
      ],
      categories: [filters.OTHERS],
    },
    {
      title: "Finite Element Simulation (FEM)",
      projectInfo:
          "Use Finite Element Method (FEM) to do a basic deformable object simulation. " +
          "Main features include euler integrator, midpoint integrator, RK4 integrator, skybox, GUI and so on.",
      technologies: "C++ / FEM / simulation / skybox / ...",
      date: "2023/3",
      url: {
        exist: false,
      },
      thumbImage: "images/projects/Others/collision.gif",
      sliderImages: [
        "images/projects/Others/cube.gif",
      ],
      categories: [filters.OTHERS],
    },
    {
      title: "Geometry Processing (Mesh)",
      projectInfo:
          "The aim of this project is to perform 4 low-level triangle manipulations, including simplification, subdivision, remeshing and denosing.",
      technologies: "C++ / simplification / subdivision / remeshing / denoising / ...",
      date: "2023/2 - 2023/3",
      url: {
        exist: false,
      },
      thumbImage: "images/projects/Others/remesh.jpg",
      sliderImages: [
        "images/projects/Others/denoise.jpg",
        "images/projects/Others/subdivide.jpg",
        "images/projects/Others/simplify.jpg",
      ],
      categories: [filters.OTHERS],
    },
  ];

  // initialize an Isotope object with configs
  useEffect(() => {
    isotope.current = new Isotope(".portfolio-filter", {
      itemSelector: ".filter-item",
      layoutMode: "masonry",
    });

    // cleanup
    return () => {
      isotope.current.destroy();
    };
  }, []);

  // handling filter key change
  useEffect(() => {
    if (imagesLoaded) {
      filterKey === "*"
        ? isotope.current.arrange({ filter: `*` })
        : isotope.current.arrange({ filter: `.${filterKey}` });
    }
  }, [filterKey, imagesLoaded]);

  const handleFilterKeyChange = (key) => () => setFilterKey(key);

  return (
    <>
      <section
        id="portfolio"
        className={"section " + (darkTheme ? "bg-dark-1" : "")}
      >
        <div className={"container " + (classicHeader ? "" : "px-lg-5")}>
          {/* Heading */}
          <div className="position-relative d-flex text-center mb-5">
            <h2
              className={
                "text-24  text-uppercase fw-600 w-100 mb-0 " +
                (darkTheme ? "text-muted opacity-1" : "text-light opacity-4")
              }
            >
              Portfolio
            </h2>
            <p
              className={
                "text-9 text-dark fw-600 position-absolute w-100 align-self-center lh-base mb-0 " +
                (darkTheme ? "text-white" : "text-dark")
              }
            >
              {" "}
              My Work
              <span className="heading-separator-line border-bottom border-3 border-primary d-block mx-auto" />
            </p>
          </div>
          {/* Heading end*/}
          {/* Filter Menu */}
          <ul
            className={
              "portfolio-menu nav nav-tabs justify-content-center border-bottom-0 mb-5 " +
              (darkTheme ? "nav-light" : "")
            }
          >
            <li className="nav-item">
              <button
                className={"nav-link " + (filterKey === "*" ? "active" : "")}
                onClick={handleFilterKeyChange("*")}
              >
                All
              </button>
            </li>
            {Object.keys(filters).map((oneKey, i) => (
              <li className="nav-item" key={i}>
                <button
                  className={
                    "nav-link " +
                    (filterKey === filters[oneKey] ? "active" : "")
                  }
                  onClick={handleFilterKeyChange(filters[oneKey])}
                >
                  {filters[oneKey]}
                </button>
              </li>
            ))}
          </ul>
          {/* Filter Menu end */}
          <div className="portfolio popup-ajax-gallery">
            <div className="row portfolio-filter filter-container g-4">
              {projectsData.length > 0 &&
                projectsData.map((project, index) => (
                  <div
                    className={
                      "col-sm-6 col-lg-4 filter-item " +
                      project.categories.join(" ")
                    }
                    key={index}
                  >
                    <div className="portfolio-box rounded">
                      <div className="portfolio-img rounded">
                        <img
                          onLoad={() => {
                            setimagesLoaded(imagesLoaded + 1);
                          }}
                          className="img-fluid d-block portfolio-image"
                          src={project.thumbImage}
                          alt=""
                        />
                        <div className="portfolio-overlay">
                          <a
                            className="popup-ajax stretched-link"
                            href=""
                            onClick={() => {
                              setSelectedProjectDetails(projectsData[index]);
                            }}
                            data-bs-toggle="modal"
                            data-bs-target="#exampleModal"
                          />
                          <div className="portfolio-overlay-details">
                            <h5 className="text-white fw-400">
                              {project.title}
                            </h5>
                            <span className="text-light">{project.categories.join(" ")}</span>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                ))}
            </div>
          </div>
        </div>
      </section>
      <div className="project-details-modal">
        {/* Modal */}
        <ProjectDetailsModal
          projectDetails={selectedProjectDetails}
          darkTheme={darkTheme}
        ></ProjectDetailsModal>
      </div>
    </>
  );
};

export default Portfolio;
